  paymentStatus?: string;
  fulfillmentStatus?: string;
  packedStatus?: string;
  returnStatus?: string;
  refundStatus?: string;
  reason?: string;
  
  // Ngu·ªìn / K√™nh
  source?: string;
  channel?: string;
  reference?: string;
  tags?: string[];
  
  // Ch√≠nh s√°ch
