  // Th√¥ng tin c∆° b·∫£n
  datePrint: string | Date;
  timePrint?: string | Date;
  timeFilter?: string;
  sourceName?: string;
  createdOn?: string | Date;
  fromDate?: string | Date;
  toDate?: string | Date;
  period?: string;
  note?: string;
  
  // Th√¥ng tin chi nh√°nh
  locationName?: string;
  accountName?: string;
  
  // T·ªïng quan b√°n h√†ng
  totalQuantityOrderFinished?: number;
  totalQuantityLineItemFulfillment?: number;
  totalQuantityLineItemReturn?: number;
  totalLineAmount?: number;
  totalOrderPayment?: number;
  totalOrderReturnPayment?: number;
  totalRealReceipt?: number;
  
  // T·ªïng k·∫øt
  totalOrders?: number;
  totalRevenue?: number;
  salesRevenue?: number;
  deliveryRevenue?: number;
  totalDiscount?: number;
  totalTax?: number;
  totalReturns?: number;
  totalCollected?: number;
  
  // Th·ª±c thu theo h√¨nh th·ª©c
  realReceiptCash?: number;
  realReceiptTransfer?: number;
  realReceiptMpos?: number;
  realReceiptCod?: number;
  realReceiptOnline?: number;
  
  // Chi ti·∫øt thanh to√°n
  cashAmount?: number;
  cardAmount?: number;
  bankTransferAmount?: number;
  codAmount?: number;
  ewalletAmount?: number;
  
  // N·ª£ c√≤n l·∫°i
  debt?: number;
  
  // T·ªïng thu
  receiptInDay?: number;
  receiptCash?: number;
  receiptTransfer?: number;
  receiptMpos?: number;
  receiptCod?: number;
  receiptOnline?: number;
  
  // T·ªïng chi
  paymentInDay?: number;
  paymentCash?: number;
  paymentTransfer?: number;
  paymentMpos?: number;
  
  // Chi ti·∫øt ƒë∆°n h√†ng b√°n
  ordersFinished?: Array<{
    stt: number;
    orderCode: string;
    amount: number;
    discount: number;
    tax: number;
    total: number;
  }>;
  
  // Chi ti·∫øt h√†ng b√°n
  itemsFulfillment?: Array<{
    stt: number;
    sku: string;
    variantName: string;
    quantity: number;
    amount: number;
  }>;
  
  // Chi ti·∫øt h√†ng tr·∫£
  itemsReturn?: Array<{
    stt: number;
    sku: string;
    variantName: string;
    quantity: number;
    amount: number;
  }>;
  
  // Chi ti·∫øt thanh to√°n
